footer {

	position: absolute;
	bottom: 0;
	left: 0;
	right: $toolbar_width;
	right: var(--toolbar_width);
	height: var(--mkl_pc-footer-height);
	z-index: 2; 
	padding: $inner_space; 
	width: auto;
	
	background: transparent;
	z-index: 2;

	display: flex;
	justify-content: space-between;
	@media (min-width: $small_screen_width) {
		justify-content: left;
	}
	align-items: center;
	box-sizing: border-box;
	
	h3.product-name {
		line-height: 1;
		margin: 0;
		@media (max-width: $small_screen_width) {
			display: none;
		}
	}

	.footer__section-left:empty {
		display: none;
	}

	.footer__section-center {
		flex: 1;
		display: flex;
		align-items: center;
		@media (max-width: $small_screen_width) {
			width: 100%;
			flex: auto;
		}
	}

	.mkl-pc-syd-modal {
		left: 0;
		&::after {
			left: 40px;
		}
	}

	@media (max-width: $medium_screen_width) {
		text-align: center;
	}
	@media (max-width: $small_screen_width) {
		border-top: 1px solid rgba(0,0,0,.2);
		padding: 0;
		right: 0;
		flex-wrap: wrap;
	}

	@media (min-width: $small_screen_width) {
		.form.form-cart {
			display: none;
		}
	}
}


&.mobile-show-form {
	footer {
		background: #FFF;
		.form.form-cart {
			bottom: 1em;
		}
	}
	.mkl-pc-show-form {
		&::before {
			opacity: 0.8;
			visibility: visible;
		}
	}
	.mkl_pc_toolbar, .mkl_pc_viewer {
		filter: blur(.3em);
	}
}